/* --- AUTH SAYFALARI İÇİN ÖZEL STİLLER --- */

#auth-page-container {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    min-height: 100vh;
    padding: 1rem;
}

.auth-container-animated {
    background-color: var(--card-background);
    border-radius: 30px;
    box-shadow: 0 5px 15px var(--shadow-dark);
    position: relative;
    overflow: hidden;
    width: 100%;
    max-width: 768px;
    min-height: 480px;
}

.auth-container-animated p {
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.3px;
    margin: 15px 0;
}

.auth-container-animated span {
    font-size: 12px;
    color: var(--light-text-color);
    margin-bottom: 10px;
}

.auth-container-animated a {
    color: var(--text-color);
    font-size: 13px;
    text-decoration: none;
    margin: 10px 0;
}

.auth-container-animated form {
    background-color: var(--card-background);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 0 40px;
    height: 100%;
}

.auth-container-animated input {
    background-color: #eee;
    border: none;
    margin: 8px 0;
    padding: 10px 15px;
    font-size: 13px;
    border-radius: 8px;
    width: 100%;
    outline: none;
    height: 40px;
    box-sizing: border-box;
}

#forgot-password-link {
    margin-top: 15px;
}

.social-icons {
    margin: 20px 0;
}

.social-icons a {
    border: 1px solid var(--border-color);
    border-radius: 20%;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    margin: 0 3px;
    width: 40px;
    height: 40px;
    transition: all 0.2s ease-in-out;
}

.social-icons a:hover {
    transform: scale(1.1);
    border-color: var(--primary-color);
}

/* Dinamik Butonlar (Giriş ve Kayıt) */
.password-strength-meter, #login-button-container {
    width: 100%;
    height: 40px;
    background-color: #e0e0e0;
    border-radius: 8px;
    margin: 15px 0;
    position: relative;
    overflow: hidden;
    cursor: not-allowed;
    transition: background-color 0.4s ease-in-out;
}

.password-strength-meter .strength-bar {
    height: 100%;
    width: 0%;
    background-color: var(--invalid-color);
    border-radius: 4px;
    transition: width 0.4s ease-in-out, background-color 0.4s ease-in-out;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
}

#strength-text, #login-text {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--text-color);
    font-size: 13px;
    font-weight: 600;
    white-space: nowrap;
    transition: color 0.4s ease-in-out;
    text-transform: uppercase;
    letter-spacing: 0.5px;
}

.strength-bar.strong { background-color: var(--valid-color); }

#login-button-container.transformed,
.password-strength-meter.transformed {
    background-color: var(--primary-color);
    cursor: pointer;
}

#login-button-container.transformed #login-text,
.password-strength-meter.transformed #strength-text {
    color: var(--white-color);
}

/* Masaüstü Geçiş Animasyonları */
@media (min-width: 769px) {
    .form-container {
        position: absolute;
        top: 0;
        height: 100%;
        transition: all 0.6s ease-in-out;
    }
    .sign-in { left: 0; width: 50%; z-index: 2; }
    .sign-up { left: 0; width: 50%; opacity: 0; z-index: 1; }
    .auth-container-animated.active .sign-in { transform: translateX(100%); opacity: 0; }
    .auth-container-animated.active .sign-up { transform: translateX(100%); opacity: 1; z-index: 5; animation: move 0.6s; }
    @keyframes move { 0%, 49.99% { opacity: 0; z-index: 1; } 50%, 100% { opacity: 1; z-index: 5; } }
    
    .toggle-container { position: absolute; top: 0; left: 50%; width: 50%; height: 100%; overflow: hidden; transition: all 0.6s ease-in-out; border-radius: 150px 0 0 150px; z-index: 1000; }
    .auth-container-animated.active .toggle-container { transform: translateX(-100%); border-radius: 0 150px 150px 0; }
    .toggle { background: linear-gradient(to right, var(--primary-color), var(--secondary-color)); color: var(--white-color); position: relative; left: -100%; height: 100%; width: 200%; transform: translateX(0); transition: all 0.6s ease-in-out; }
    .auth-container-animated.active .toggle { transform: translateX(50%); }
    .toggle-panel { position: absolute; width: 50%; height: 100%; display: flex; align-items: center; justify-content: center; flex-direction: column; padding: 0 30px; text-align: center; top: 0; transform: translateX(0); transition: all 0.6s ease-in-out; cursor: pointer; }
    .toggle-left { transform: translateX(-200%); }
    .auth-container-animated.active .toggle-left { transform: translateX(0); }
    .toggle-right { right: 0; transform: translateX(0); }
    .auth-container-animated.active .toggle-right { transform: translateX(200%); }
    .toggle-panel .app-logo { width: 80px; height: 80px; margin-bottom: 1rem; }
}

/* Mobil Görünüm */
.mobile-logo-container { display: none; }
.mobile-toggle-form { display: none; }

@media (max-width: 768px) {
    #auth-page-container { padding: 0; }
    .auth-container-animated { max-width: 100%; min-height: 100vh; border-radius: 0; box-shadow: none; display: block; }
    .toggle-container { display: none; }
    .form-container { position: relative; width: 100%; height: auto; padding: 40px 20px; }
    .mobile-toggle-form { margin-top: 20px; display: block; text-align: center; }
    .mobile-toggle-form a { font-weight: bold; color: var(--primary-color); cursor: pointer; }
    .mobile-logo-container { display: block; width: 100%; text-align: center; padding: 2rem 0 1rem 0; }
    .mobile-app-logo { width: 60px; height: 60px; }
}

/* Diğer Auth Sayfaları (Şifre Sıfırlama, Doğrulama vb.) */
.auth-container-standalone { background-color: var(--card-background); border-radius: 30px; box-shadow: 0 5px 15px var(--shadow-dark); position: relative; width: 100%; max-width: 480px; padding: 40px; overflow: hidden; }
.auth-container-standalone form { width: 100%; display: flex; flex-direction: column; align-items: center; }
.auth-container-standalone form.hidden { display: none; }
.auth-container-standalone h1 { margin-bottom: 1rem; text-align: center; }
.auth-container-standalone span { font-size: 14px; color: var(--light-text-color); margin-bottom: 1.5rem; text-align: center; display: block; }
.auth-container-standalone .back-to-login { margin-top: 1.5rem; font-size: 14px; }
.auth-container-standalone button { width: 100%; background-color: var(--primary-color); color: var(--white-color); font-size: 13px; padding: 10px 45px; border: 1px solid transparent; border-radius: 8px; font-weight: 600; letter-spacing: 0.5px; text-transform: uppercase; margin-top: 10px; cursor: pointer; transition: all 0.2s ease-in-out; height: 40px; box-sizing: border-box; }
.auth-container-standalone button:disabled { background-color: #e0e0e0; cursor: not-allowed; }
.auth-container-standalone input { background-color: #eee; border: none; margin: 8px 0; padding: 10px 15px; font-size: 13px; border-radius: 8px; width: 100%; outline: none; height: 40px; box-sizing: border-box; }
.security-note { margin-top: 1.5rem; font-size: 0.9rem; color: var(--light-text-color); text-align: center; width: 100%; }
.security-note p { margin-bottom: 0.5rem; }
.security-note i { margin-right: 0.5rem; color: var(--primary-color); }