#auth-page-container {
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100vh;
  padding: 1rem;
  display: flex;
}

.auth-container-animated {
  background-color: var(--card-background);
  box-shadow: 0 5px 15px var(--shadow-dark);
  border-radius: 30px;
  width: 100%;
  max-width: 768px;
  min-height: 480px;
  position: relative;
  overflow: hidden;
}

.auth-container-animated p {
  letter-spacing: .3px;
  margin: 15px 0;
  font-size: 14px;
  line-height: 20px;
}

.auth-container-animated span {
  color: var(--light-text-color);
  margin-bottom: 10px;
  font-size: 12px;
}

.auth-container-animated a {
  color: var(--text-color);
  margin: 10px 0;
  font-size: 13px;
  text-decoration: none;
}

.auth-container-animated form {
  background-color: var(--card-background);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  padding: 0 40px;
  display: flex;
}

.auth-container-animated input {
  box-sizing: border-box;
  background-color: #eee;
  border: none;
  border-radius: 8px;
  outline: none;
  width: 100%;
  height: 40px;
  margin: 8px 0;
  padding: 10px 15px;
  font-size: 13px;
}

#forgot-password-link {
  margin-top: 15px;
}

.social-icons {
  margin: 20px 0;
}

.social-icons a {
  border: 1px solid var(--border-color);
  border-radius: 20%;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  margin: 0 3px;
  transition: all .2s ease-in-out;
  display: inline-flex;
}

.social-icons a:hover {
  border-color: var(--primary-color);
  transform: scale(1.1);
}

.password-strength-meter, #login-button-container {
  cursor: not-allowed;
  background-color: #e0e0e0;
  border-radius: 8px;
  width: 100%;
  height: 40px;
  margin: 15px 0;
  transition: background-color .4s ease-in-out;
  position: relative;
  overflow: hidden;
}

.password-strength-meter .strength-bar {
  background-color: var(--invalid-color);
  z-index: 1;
  border-radius: 4px;
  width: 0%;
  height: 100%;
  transition: width .4s ease-in-out, background-color .4s ease-in-out;
  position: absolute;
  top: 0;
  left: 0;
}

#strength-text, #login-text {
  z-index: 2;
  width: 100%;
  height: 100%;
  color: var(--text-color);
  white-space: nowrap;
  text-transform: uppercase;
  letter-spacing: .5px;
  justify-content: center;
  align-items: center;
  font-size: 13px;
  font-weight: 600;
  transition: color .4s ease-in-out;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.strength-bar.strong {
  background-color: var(--valid-color);
}

#login-button-container.transformed, .password-strength-meter.transformed {
  background-color: var(--primary-color);
  cursor: pointer;
}

#login-button-container.transformed #login-text, .password-strength-meter.transformed #strength-text {
  color: var(--white-color);
}

@media (width >= 769px) {
  .form-container {
    height: 100%;
    transition: all .6s ease-in-out;
    position: absolute;
    top: 0;
  }

  .sign-in {
    z-index: 2;
    width: 50%;
    left: 0;
  }

  .sign-up {
    opacity: 0;
    z-index: 1;
    width: 50%;
    left: 0;
  }

  .auth-container-animated.active .sign-in {
    opacity: 0;
    transform: translateX(100%);
  }

  .auth-container-animated.active .sign-up {
    opacity: 1;
    z-index: 5;
    animation: .6s move;
    transform: translateX(100%);
  }

  @keyframes move {
    0%, 49.99% {
      opacity: 0;
      z-index: 1;
    }

    50%, 100% {
      opacity: 1;
      z-index: 5;
    }
  }

  .toggle-container {
    z-index: 1000;
    border-radius: 150px 0 0 150px;
    width: 50%;
    height: 100%;
    transition: all .6s ease-in-out;
    position: absolute;
    top: 0;
    left: 50%;
    overflow: hidden;
  }

  .auth-container-animated.active .toggle-container {
    border-radius: 0 150px 150px 0;
    transform: translateX(-100%);
  }

  .toggle {
    background: linear-gradient(to right, var(--primary-color), var(--secondary-color));
    color: var(--white-color);
    width: 200%;
    height: 100%;
    transition: all .6s ease-in-out;
    position: relative;
    left: -100%;
    transform: translateX(0);
  }

  .auth-container-animated.active .toggle {
    transform: translateX(50%);
  }

  .toggle-panel {
    text-align: center;
    cursor: pointer;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 50%;
    height: 100%;
    padding: 0 30px;
    transition: all .6s ease-in-out;
    display: flex;
    position: absolute;
    top: 0;
    transform: translateX(0);
  }

  .toggle-left {
    transform: translateX(-200%);
  }

  .auth-container-animated.active .toggle-left {
    transform: translateX(0);
  }

  .toggle-right {
    right: 0;
    transform: translateX(0);
  }

  .auth-container-animated.active .toggle-right {
    transform: translateX(200%);
  }

  .toggle-panel .app-logo {
    width: 80px;
    height: 80px;
    margin-bottom: 1rem;
  }
}

.mobile-logo-container, .mobile-toggle-form {
  display: none;
}

@media (width <= 768px) {
  #auth-page-container {
    padding: 0;
  }

  .auth-container-animated {
    max-width: 100%;
    min-height: 100vh;
    box-shadow: none;
    border-radius: 0;
    display: block;
  }

  .toggle-container {
    display: none;
  }

  .form-container {
    width: 100%;
    height: auto;
    padding: 40px 20px;
    position: relative;
  }

  .mobile-toggle-form {
    text-align: center;
    margin-top: 20px;
    display: block;
  }

  .mobile-toggle-form a {
    color: var(--primary-color);
    cursor: pointer;
    font-weight: bold;
  }

  .mobile-logo-container {
    text-align: center;
    width: 100%;
    padding: 2rem 0 1rem;
    display: block;
  }

  .mobile-app-logo {
    width: 60px;
    height: 60px;
  }
}

